






import { Component, Prop, Vue } from 'vue-property-decorator';

@Component
export default class AppButton extends Vue {
    @Prop() private click!: (...args: any[]) => void;
    @Prop({ default: false }) private disabled!: boolean;

    private onClick(e: Event): void {
        e.preventDefault();
        if (!this.disabled) {
            this.click();
        }
    }

}
